/* tailwind css */
@tailwind base;
@tailwind components;
@tailwind utilities;

html {
	font-family: sans-serif; /* 1 */
	-ms-text-size-adjust: 100%; /* 2 */
	-webkit-text-size-adjust: 100%; /* 2 */
}

body {
	color: #333;
	margin: 0;
}

.container {
	position: absolute;
	top: 50%;
	left: 0;
	right: 0;
	padding: 0 15px;
	margin: 0 auto;
	text-align: center;
	transform: translateY(-50%);
	-webkit-transform: translateY(-50%);
	-moz-transform: translateY(-50%);
	-ms-transform: translateY(-50%);
}

h1 {
	font-family: monospace;
	font-size: 24px;
	font-weight: bold;
	margin: 10px 0;
}

ul {
	list-style-type: none;
}

p {
	margin: 0 0 10px 0;
	font-family: 'Noto Sans', sans-serif;
	font-size: 12px;
	font-weight: 400;
	letter-spacing: 1px;
}

a {
	text-decoration: none;
	transition: all 0.25s;
	-webkit-transition: all 0.25s;
	-moz-transition: all 0.25s;
	-ms-transition: all 0.25s;
}

a:hover {
	transition: all 0.25s;
	-webkit-transition: all 0.25s;
	-moz-transition: all 0.25s;
	-ms-transition: all 0.25s;
}

p.company a {
	font-family: monospace;
	font-size: 12px;
	color: #333;
	font-weight: bold;
	letter-spacing: 1px;
}

p.company a:hover span.blue {
	color: #0021a5;
	transition: all 0.25s;
	-webkit-transition: all 0.25s;
	-moz-transition: all 0.25s;
	-ms-transition: all 0.25s;
}

p.company a:hover span.orange {
	color: #fa4616;
	transition: all 0.25s;
	-webkit-transition: all 0.25s;
	-moz-transition: all 0.25s;
	-ms-transition: all 0.25s;
}

p.skills span {
	font-family: monospace;
	font-weight: bold;
}

p.contact {
	margin: 15px 0;
	line-height: 25px;
}

a.email {
	display: inline-block;
	width: 100px;
	height: 25px;
	line-height: 25px;
	font-family: monospace;
	font-size: 12px;
	color: #fff;
	font-weight: bold;
	letter-spacing: 1px;
	background-color: #0021a5;
	padding: 0;
}

a.email:hover {
	background-color: #fa4616;
	color: #0021a5;
}

.projects {
	margin: 0 0 10px 0;
	padding: 0;
}

.projects li {
	margin-bottom: 5px;
}

.projects a {
	font-family: monospace;
	font-size: 12px;
	color: #333;
	font-weight: bold;
	letter-spacing: 1px;
	padding-bottom: 3px;
	border-bottom: 3px solid transparent;
}

.projects a:hover {
	color: #fa4616;
	padding-bottom: 3px;
	border-bottom: 3px solid #0021a5;
}

