footer {
	position: fixed;
	bottom: 0;
	left: 0;
	right: 0;
	margin: 10px 15px;
	font-family: arial;
	font-size: 11px;
	letter-spacing: 0.1em;
	color: #a6a6a6;
	text-align: center;
}
